@import '~assets/styles/h3Imports';

.root {
  .validation {
    color: var(--interface-error);
    font-size: rem(14);
    font-weight: 600;
  }

  .groups {
    display: flex;
    flex-direction: column;
    gap: em(24);
    margin: em(16) em(16) em(24);
  }

  &[data-time-picker-devider='true'] {
    border-top: 1px solid var(--modal-button-border);
    margin-top: var(--spacing-s);
    padding-top: var(--spacing-xs);

    @media (max-width: $max-phone-size) {
      margin-top: var(--spacing-xs);
    }
  }

  &[data-time-picker-devider='false'] {
    margin-top: var(--spacing-m);

    @media (max-width: $max-tablet-size) {
      margin-top: var(--spacing-s);
    }

    @media (max-width: $max-phone-size) {
      margin-top: var(--spacing-xs);
    }
  }
}
