@import '~assets/styles/h3Imports';

.root {
  border-color: var(--gray-1);
  border-width: 1px;
  color: var(--accent-interactive);

  .time {
    font-size: rem(16);
  }

  .midday {
    color: var(--gray-3);
    font-weight: 600;
  }
}

.root.selected {
  background-color: var(--accent-interactive);
  border-color: var(--accent-interactive);

  .midday,
  .time {
    color: var(--white);
  }
}
