@import '~assets/styles/h3Imports';

.root {
  align-items: center;
  display: flex;
  flex-direction: column;
  text-align: center;

  @media #{$tablet-down} {
    margin: auto;
    max-width: 270px;
  }

  .title {
    @include h5;

    color: var(--gray-3);
    margin-bottom: rem(4);
    margin-top: rem(16);
  }

  .description {
    @include paragraph;

    color: var(--gray-3);
    margin-bottom: rem(16);
  }

  .button {
    padding: em(4, 14) em(14, 14);
  }
}
