@import '~assets/styles/h3Imports';

.root {
  .header {
    align-items: center;
    color: var(--gray-4);
    display: flex;
    font-size: rem(15);
    font-weight: 600;
    gap: em(8);
    margin: 0 0 em(10);
  }

  .list {
    display: flex;
    flex-wrap: wrap;
    gap: em(8);
    list-style-type: none;
    margin: 0;
    padding: 0;

    .list-item {
      margin: 0;
    }
  }
}
